"use client";

import { useState } from "react";

export default function Header() {
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);

  return (
    <header className="mad-header header-2">
      <div className="mad-header-section--sticky-xl">
        <div className="container">
          <div className="row align-items-center">
            {/* Logo */}
            <div className="col-xl-3 col-lg-4 col-sm-3 col-6">
              <a href="/" className="mad-logo">
                <img src="/img/logo.png" alt="Logo" />
              </a>
            </div>

            {/* Desktop nav */}
            <div className="col-xl-6 col-lg-4 col-sm-3 col-6 desktop-nav">
              <nav className="mad-navigation-container">
                <ul className="mad-navigation mad-navigation--vertical-sm">
                  <li>
                    <a href="#">Home</a>
                  </li>
                  <li>
                    <a href="#">Shop</a>
                  </li>
                  <li>
                    <a href="#">Pages</a>
                  </li>
                  <li>
                    <a href="#">Blog</a>
                  </li>
                </ul>
              </nav>
            </div>

            {/* Mobile hamburger */}
            <div className="col-6 d-xl-none text-end">
              <button
                className={`hamburger ${mobileMenuOpen ? "open" : ""}`}
                onClick={() => setMobileMenuOpen(!mobileMenuOpen)}
                aria-label="Toggle menu"
              >
                <span></span>
                <span></span>
                <span></span>
              </button>
            </div>
          </div>
        </div>
      </div>

      {/* Mobile menu */}
      <nav className={`nav-mobile ${mobileMenuOpen ? "active" : ""}`}>
        <ul>
          <li>
            <a href="#">Home</a>
          </li>
          <li>
            <a href="#">Shop</a>
          </li>
          <li>
            <a href="#">Pages</a>
          </li>
          <li>
            <a href="#">Blog</a>
          </li>
          <li>
            <a href="#">Contact</a>
          </li>
        </ul>
      </nav>
    </header>
  );
}
